  scroll-padding-top: 80px;
}

.page {
  overflow: hidden;
}

body {
  font-family: var(--main-font);
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
