.btn {
  font-family: var(--main-font);
  min-width: 208px;
  min-height: 44px;
  color: var(--white-cl);
  border-radius: var(--btn-radius);
  transition: background-color var(--anim-fast);
  background-color: #e86f54;
  border: none;
  margin: 0 auto;
  padding: 10px 35px;
  font-size: 20px;
  font-weight: 500;
  line-height: .857;
  display: block;
}

.btn:hover {
  background-color: var(--hover-btn-cl);
}

.btn:focus {
  background-color: var(--focus-btn-cl);
}

@media screen and (min-width: 768px) {
  .btn {
    min-width: 280px;
    min-height: 64px;
    color: var(--white-cl);
    padding: 20px 43.5px;
    font-size: 28px;
    font-weight: 500;
    line-height: 1.2;
  }
}

.header {
  width: 100vw;
  min-height: 60px;
  background-color: var(--white-cl);
  z-index: 1000;
  padding-top: 16px;
  position: fixed;
  top: 0;
  left: 0;
}

.header:has(.mobile-menu__btn.is-active) {
  background-color: rgba(0, 0, 0, 0);
}

.mobile-menu__btn.is-active ~ .logo {
  opacity: 0;
}

.logo:hover .logo__icon {
  fill: var(--hover-btn-cl);
}

.logo__icon {
  width: 54px;
  height: 42px;
  fill: #000;
  margin-right: 5px;
  transition: fill .3s ease-in-out;
}

@media screen and (min-width: 768px) {
  .logo__icon {
    width: 86px;
    height: 68px;
  }
}

.header__wrapper {
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__wrapper {
    align-items: center;
    gap: 68px;
  }
}

@media screen and (min-width: 1280px) {
  .header__wrapper {
    align-items: center;
    gap: 261px;
  }
}

.header__tel-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
  margin-right: 14px;
  padding: 6px 0 0;
  transition: animation, fill 1.5s ease-in-out;
  animation: phone-infinite 10s ease-in-out infinite;
}

@media screen and (min-width: 768px) {
  .header__tel-btn {
    margin-left: 0;
    margin-right: 0;
    top: 24px;
    right: 24px;
  }
}

.mobile-menu__btn.is-active ~ .header__tel-btn {
  opacity: 0;
}

.header__tel-btn:hover, .header__tel-btn:focus {
  fill: var(--default-btn-cl);
  animation: phone-hover 10s ease-in-out;
}

.header__icon {
  width: 25px;
  height: 25px;
}

.navigation {
  display: none;
}

@media screen and (min-width: 768px) {
  .navigation {
    margin-right: auto;
    display: block;
  }
}

.nav__list {
  margin-right: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .nav__list {
    justify-content: center;
    align-items: center;
    gap: 24px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
