    gap: 48px;
  }
}

.nav__link {
  color: var(--main-text-cl);
  padding-top: 16px;
  padding-bottom: 17px;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

@media screen and (min-width: 768px) {
  .nav__link {
    padding-top: 12px;
    padding-bottom: 21px;
  }
}

.nav__link:after {
  content: "";
  background-color: var(--default-btn-cl);
  width: 0%;
  height: .5px;
  transition: width .3s ease-in-out;
  display: block;
}

.nav__link:hover, .nav__link:focus {
  color: var(--default-btn-cl);
}

.nav__link:hover:after, .nav__link:focus:after {
  content: "";
  background-color: var(--default-btn-cl);
  width: 100%;
  height: 1px;
  display: block;
}

@keyframes phone-infinite {
  0% {
    transform: rotate(0) scale(1) skew(1deg);
  }

  3% {
    transform: rotate(-30deg)scale(1.05)skew(1deg);
  }

  6% {
    transform: rotate(30deg)scale(1.05)skew(1deg);
  }

  9% {
    transform: rotate(-30deg)scale(1.1)skew(1deg);
  }

  12% {
    transform: rotate(30deg)scale(1.05)skew(1deg);
  }

  15% {
    transform: rotate(0) scale(1) skew(1deg);
  }

  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}

@keyframes phone-hover {
  0% {
    transform: rotate(0) scale(1) skew(1deg);
  }

  2% {
    transform: rotate(-30deg)scale(1.03)skew(1deg);
  }

  4% {
    transform: rotate(30deg)scale(1.05)skew(1deg);
  }

  6% {
    transform: rotate(-30deg)scale(1.1)skew(1deg);
  }

  8% {
    transform: rotate(30deg)scale(1.1)skew(1deg);
  }

  10% {
    transform: rotate(0) scale(1) skew(1deg);
  }

  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}

